export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static extractClipNames(data) {
    return data.items.map(clip => clip.snippet.title);
  }

  static extractClipIds(data) {
    return data.items.map(clip => clip.id.videoId);
  }

  static extractClipDescriptions(data) {
    return data.items.map(clip => clip.snippet.description);
  }

  static extractClipImages(data) {
    return data.items.map(clip => clip.snippet.thumbnails.high.url);
  }

  static extractChannelTitles(data) {
    return data.items.map(clip => clip.snippet.channelTitle);
  }

  static extractPublicationDate(data) {
    return data.items.map(clip => clip.snippet.publishedAt);
  }

  static extractViewCounts(data) {
    return data.items.map(clip => clip.statistics.viewCount);
  }

  async getClipNames() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractClipNames(data);
  }

  async getClipIds() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();
    return AppModel.extractClipIds(data);
  }

  async getClipDescriptions() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractClipDescriptions(data);
  }

  async getClipImages() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractClipImages(data);
  }

  async getChannelTitles() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractChannelTitles(data);
  }

  async getPublicationDate() {
    const { url } = this.state;

    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractPublicationDate(data);
  }

  // eslint-disable-next-line class-methods-use-this
  async getViewCounts(ids) {
    const url = `https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=${ids.join()}&part=snippet,statistics`;

    const responce = await fetch(url);
    const data = await responce.json();

    return AppModel.extractViewCounts(data);
  }
}
