/* eslint-disable no-useless-escape */

export default class AppView {
  constructor(titles, ids, descriptions, images, channelTitles, publicationDates, viewCounts) {
    this.titles = titles;
    this.ids = ids;
    this.descriptions = descriptions;
    this.images = images;
    this.channelTitles = channelTitles;
    this.publicationDates = publicationDates;
    this.viewCounts = viewCounts;
  }

  render() {
    const section = document.getElementById('cards-section');
    const content = document.createElement('ul');
    let clipsPerPage;

    content.classList.add('container');
    content.innerHTML = this.titles.map((title, index) => `
      <li class ="clip">
        <div class="clip-inner">
          <img src=${this.images[index]}>
          <a href=https://www.youtube.com/watch?v=${this.ids[index]} class="title" target=_blank>${title}</a>
          <div class="clip-info">
            <ul class="clip-info-wrapper">
            <li class="stats"><i class="fas fa-at"></i>${this.channelTitles[index]}</li>
            <li class="stats"><i class="fas fa-calendar-week"></i>${`${this.publicationDates[index]}`.slice(0, 10)}</li>
            <li class="stats"><i class="far fa-eye"></i>${(+this.viewCounts[index]).toLocaleString('ru')}</li>
            </ul>
            <p class="clip-info-wrapper">
                ${this.descriptions[index]}
            </p>
          </div>
        </div>
      </li>
    `).join('');
    section.appendChild(content);

    content.style.setProperty('--i', 0);


    document.body.appendChild(section);


    function setClipsPerPage() {
      if (window.innerWidth >= 1200) {
        clipsPerPage = 4;
      }

      if ((window.innerWidth >= 1000) && (window.innerWidth <= 1200)) {
        clipsPerPage = 3;
      }

      if ((window.innerWidth >= 700) && (window.innerWidth <= 1000)) {
        clipsPerPage = 2;
      }

      if ((window.innerWidth <= 700)) {
        clipsPerPage = 1;
      }
    }

    function slider() {
      const container = document.querySelector('.container');
      const N = container.children.length / clipsPerPage;
      let i = container.style.getPropertyValue('--i');
      let x0 = null;
      let locked = false;
      let w;

      function unify(e) {
        return e.changedTouches ? e.changedTouches[0] : e;
      }

      function lock(e) {
        x0 = unify(e).clientX;
        container.classList.toggle('smooth', !(locked = true));
      }

      function size() {
        w = window.innerWidth;
      }

      function move(e) {
        if (locked) {
          const dx = unify(e).clientX - x0;
          const s = Math.sign(dx);
          let f = +(s * dx / w).toFixed(2);

          if ((i > 0 || s < 0) && (i < N - 1 || s > 0) && f > 0.2) {
            container.style.setProperty('--i', i -= s);
            f = 1 - f;
          }

          container.style.setProperty('--tx', '0px');
          container.style.setProperty('--f', f);
          container.classList.toggle('smooth', !(locked = false));
          x0 = null;
        }
      }

      size();

      document.addEventListener('resize', size, false);

      container.style.setProperty('--n', N);

      container.addEventListener('mousedown', lock, false);
      container.addEventListener('touchstart', lock, false);

      container.addEventListener('mouseup', move, false);
      container.addEventListener('touchend', move, false);
      container.addEventListener('touchmove', (e) => { e.preventDefault(); }, false);

      function drag(e) {
        e.preventDefault();
        if (locked) {
          container.style.setProperty('--tx', `${Math.round(unify(e).clientX - x0)}px`);
        }
      }


      container.addEventListener('mousemove', drag, false);
      container.addEventListener('touchmove', drag, false);
    }


    setClipsPerPage();
    slider();


    window.addEventListener('resize', () => {
      setClipsPerPage();
      const container = document.querySelector('.container');
      const currentI = container.style.getPropertyValue('--i') || 0;
      slider();

      if (currentI >= container.style.getPropertyValue('--n')) {
        container.style.setProperty('--i', Math.floor(container.style.getPropertyValue('--n')));
      }
    });
  }
}
