import AppModel from '../models/AppModel';
import AppView from '../views/AppView';

export default class App {
  constructor() {
    this.state = {
      url: '',
    };

    this.key = 'AIzaSyDnLG86DcuGwyPMvHHBGZYA65N80ticl1I';// my key
    // this.key = 'AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y';
  }

  async createForm() {
    const searchSection = document.createElement('section');
    const textArea = document.createElement('input');
    const cardsSection = document.createElement('section');

    textArea.setAttribute('placeholder', 'video?');
    textArea.setAttribute('type', 'search');

    searchSection.classList.add('search-block');
    cardsSection.classList.add('cards-block');
    cardsSection.id = 'cards-section';

    searchSection.appendChild(textArea);
    document.body.appendChild(searchSection);
    document.body.appendChild(cardsSection);

    textArea.addEventListener('search', () => {
      if (textArea.value.length === 0) {
        cardsSection.innerHTML = '';
      } else {
        this.state.url = `https://www.googleapis.com/youtube/v3/search?key=${this.key}&type=video&part=snippet&maxResults=16&q=${textArea.value}`;
        cardsSection.innerHTML = '';
        this.update();
      }
    });
  }

  async update() {
    const model = new AppModel(this.state);
    const data = await model.getClipNames();
    const ids = await model.getClipIds();
    const desc = await model.getClipDescriptions();
    const images = await model.getClipImages();
    const channelTitles = await model.getChannelTitles();
    const publicationDates = await model.getPublicationDate();
    const viewCounts = await model.getViewCounts(ids);
    const view = new AppView(data, ids, desc, images, channelTitles, publicationDates, viewCounts);


    function createButtons() {
      const buttonsSection = document.createElement('section');
      buttonsSection.classList.add('buttons-block');
      const backwardButton = document.createElement('button');
      const currentButton = document.createElement('button');
      const forwardButton = document.createElement('button');

      backwardButton.innerText = '<<';
      currentButton.innerText = '<';
      forwardButton.innerText = '>';

      forwardButton.addEventListener('click', () => {
        const container = document.querySelector('.container');
        const currentI = container.style.getPropertyValue('--i') || 0;
        if (+currentI + 1 < container.style.getPropertyValue('--n')) {
          container.style.setProperty('--i', +currentI + 1);
        }
      });

      currentButton.addEventListener('click', () => {
        const container = document.querySelector('.container');
        const currentI = container.style.getPropertyValue('--i') || 0;
        if (+currentI - 1 >= 0) {
          container.style.setProperty('--i', +currentI - 1);
        }
      });

      backwardButton.addEventListener('click', () => {
        const container = document.querySelector('.container');
        container.style.setProperty('--i', 0);
      });

      buttonsSection.appendChild(backwardButton);
      buttonsSection.appendChild(currentButton);
      buttonsSection.appendChild(forwardButton);

      document.body.appendChild(buttonsSection);
    }
    view.render();
    createButtons();
  }
}
